footer {
  background: #000;
  display: flex;
  flex-direction: column;
  flex: 1 1;
  justify-content: center;
  align-items: center;
}

.first-part {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

a {
  font-weight: 600;
  color: #fff;
  text-decoration: none;
}

a:hover {
  color: #1bd760;
}

p.title {
  font-size: 12px;
  color: #7c7f81;
  margin-bottom: 15px;
}

.footer {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

svg {
  width: 132px;
  fill: #fff;
  -webkit-transform: translateY(20%);
          transform: translateY(20%);
}

.apps {
  width: 10%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
}

.apps > div + div {
  margin-left: 5px;
}

.apps > div {
  border: 1px solid #222326;
  padding: 20px;
  background: #222326;
  border-radius: 50%;
}

.apps i {
  color: #fff;
  font-size: 1.5rem;
}

.apps i:hover {
  color: #1bd760;
}
.second-part {
  margin: 30px 0px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

/* .second-part-first {
} */

/* .second-part-first > a + a {
  margin-right: 20px;
  margin-left: 20px;
} */

.second-part-first > a {
  color: #7c7f81;
  font-size: 12px;
}
.second-part-first > a:hover {
  color: #1bd760;
}

.second-part-second > p {
  font-size: 12px;
  color: #7c7f81;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #2941ab;
  min-height: 846px;
  background-image: url("https://www-growth.scdn.co/static/home/bursts.svg");
  background-size: 175%;
  background-position: 46% 4%;
  background-repeat: no-repeat;
}

section h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  font-size: 156px;
  color: #1ed760;
}

.container {
  width: 800px;
  text-indent: -20em;
  -webkit-transform: translate(135px, 0px);
          transform: translate(135px, 0px);
  margin-bottom: -60px;
}
/* #2940AB */
p {
  font-size: 20px;
  font-weight: 500;
  color: #1ed760;
}
button {
  padding: 15px 20px;
  color: #2940ab;
  font-weight: 600;
  border: 1px solid #1ed760;
  background: #1ed760;
  border-radius: 25px;
}

button:hover {
  cursor: pointer;
}

.navbar {
  font-family: Helvetica, sans-serif;
  background: #000;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
}
.nav-links {
  display: flex;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
}

/* .main-links > a :hover {
  color: aqua;
  color: #1bd760 !important;
} */

.nav-links a:hover {
  color: #1bd760;
}

.main-links {
  margin-right: 20px;
}

.main-links > a + a {
  margin-left: 25px;
}

.main-links:nth-child(1)::after {
  color: white;
  content: "|";
  margin-left: 20px;
}

.signup-links > a + a {
  margin-left: 20px;
}

.navbar-logo > svg {
  -webkit-transform: translateX(-55%);
          transform: translateX(-55%);
  width: 132px;
  fill: #fff;
}

